.wbk_booking_form__wrapper * {
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
}

.wbk_booking_form__wrapper {
  position: relative;
  width: calc(100vw - 32px);
  text-align: left;
  max-width: min(750px, 100%);
  flex-shrink: 0;
  flex-grow: 0;
  align-self: center;
  justify-self: center;
}
.wbk_booking_form__wrapper--compact-form {
  max-width: 500px;
  background-color: #ffffff;
  border-radius: 8px;
}
.wbk_booking_form__wrapper__loading-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.wbk_booking_form__wrapper:not(.wbk_booking_form__wrapper--expanded) .wbk_booking_form__left-panel {
  max-width: 100%;
  width: 100%;
}
.wbk_booking_form__content {
  padding: 24px;
  display: flex;
  flex-flow: column;
  gap: 16px;
  height: calc(100% - 80px);
}
.wbk_booking_form__left-panel {
  max-width: calc(100% - 250px);
  width: 100%;
  transition: 0.3s all linear;
  display: flex;
  flex-direction: column;
  max-height: inherit;
}
.wbk_booking_form__left-panel :global(.custom-scroll-handle) {
  left: 4px !important;
}
.wbk_booking_form__right-panel {
  margin-left: auto;
}
.wbk_booking_form__body__wrapper {
  max-width: 750px;
  min-height: 750px;
  box-shadow: 0px 0px 16px 0px rgba(63, 63, 70, 0.1607843137);
  width: 100%;
  border-radius: 8px;
  display: flex;
  flex-flow: column;
}
.wbk_booking_form__body__inner-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 750px;
  min-height: 750px;
  max-height: 750px;
  background-color: #ffffff;
  border-radius: 8px;
}
.wbk_booking_form__body__inner-wrapper--bottom-view {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
@media screen and (max-width: 768px) {
  .wbk_booking_form {
    min-width: 100%;
    max-width: 100%;
    position: relative;
  }
  .wbk_booking_form__left-panel {
    max-width: 100%;
    width: 100%;
  }
}