.wbk_thank_you__wrapper {
  width: 100%;
  max-width: 500px;
  min-height: 750px;
  max-height: 750px;
  border-radius: 8px;
  box-shadow: 0px 0px 16px 0px rgba(63, 63, 70, 0.1607843137);
  padding: 32px 24px;
  background-color: #fff;
}
.wbk_thank_you__wrapper h1,
.wbk_thank_you__wrapper h3 {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5%;
  margin: 0;
}
.wbk_thank_you__wrapper h4 {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5%;
  margin: 0;
}
.wbk_thank_you__wrapper p,
.wbk_thank_you__wrapper li {
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1%;
  color: #668091;
  margin: 0;
}
.wbk_thank_you__wrapper strong {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  color: #22292f;
}
.wbk_thank_you__header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 24px;
  width: 100%;
}
.wbk_thank_you__header__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 8px;
  text-align: center;
}
.wbk_thank_you__header img {
  width: 88px;
  height: 88px;
  max-width: 88px;
  max-height: 88px;
  object-fit: contain;
}
.wbk_thank_you__header--hidden {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  transform: translateY(40px);
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1), transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}
.wbk_thank_you__header--visible {
  opacity: 1;
  position: relative;
  transform: translateY(0);
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1), transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}
.wbk_thank_you__outer-wrapper {
  width: 100%;
  max-width: 750px;
  min-height: 750px;
  max-height: 750px;
  border-radius: 8px;
  box-shadow: 0px 0px 16px 0px rgba(63, 63, 70, 0.1607843137);
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: flex-start;
  margin: 0 auto;
}
.wbk_thank_you__inner-wrapper {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: flex-start;
  gap: 16px;
  height: 100%;
}
.wbk_thank_you__information {
  margin-top: auto;
  width: 100%;
}
.wbk_thank_you__information ul {
  margin-top: 12px;
}
.wbk_thank_you__booking_items {
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 12px;
  width: 100%;
}
.wbk_thank_you__panel-wrapper {
  padding: 16px;
  border: 1.5px solid #edeff2;
  border-radius: 8px;
  width: 100%;
}
.wbk_thank_you__cart-items {
  margin-top: 12px;
  display: flex;
  flex-flow: column;
}
.wbk_thank_you__cart-items__item {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}
.wbk_thank_you__cart-items__item--total {
  padding-top: 20px;
  margin-top: 4px;
  border-top: 1px solid #edeff2;
}
.wbk_thank_you__cart-items__item--total strong {
  color: var(--wbk-primary-500);
}
.wbk_thank_you__custom-scroll {
  width: 100%;
}
.wbk_thank_you__calendar--hidden {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  transform: translateY(40px);
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1), transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}
.wbk_thank_you__calendar--visible {
  opacity: 1;
  position: relative;
  transform: translateY(0);
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1), transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.wbk_cancel_icon_bg {
  background: rgba(220, 38, 38, 0.12);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 72px;
  height: 72px;
}
.wbk_cancel_icon_bg img {
  width: 72px;
  height: 72px;
  max-width: 72px;
  max-height: 72px;
  object-fit: contain;
}