.wbk_step__separator {
  width: 100%;
  height: 1px;
  background-color: #edeff2;
}
.wbk_step__scroll-wrapper .rcs-custom-scrollbar {
  width: 3px;
  right: 0;
  padding: 0;
}
.wbk_step__payment-step {
  display: flex;
  flex-flow: column;
  gap: 16px;
}
.wbk_step__certificates {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8px;
}
.wbk_step__certificates img {
  filter: grayscale(1);
  opacity: 0.5;
}
.wbk_step__form-wrapper {
  padding-right: 8px;
}
.wbk_step__native-scroll-wrapper {
  overflow-y: auto;
  overflow-x: hidden;
  min-height: 0;
  flex: 1;
  scroll-behavior: smooth;
  /* Slim custom scrollbar for Webkit browsers */
  /* Slim custom scrollbar for Firefox */
  scrollbar-width: thin;
  scrollbar-color: #d1d5db transparent;
}
.wbk_step__native-scroll-wrapper::-webkit-scrollbar {
  width: 6px;
  background: transparent;
}
.wbk_step__native-scroll-wrapper::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 4px;
  transition: background 0.2s;
}
.wbk_step__native-scroll-wrapper::-webkit-scrollbar-thumb:hover {
  background: #b0b8c1;
}

.wbk_coupon__wrapper {
  display: flex;
  align-items: center;
  gap: 12px;
}
.wbk_coupon__wrapper input[type=text] {
  border-radius: 8px;
  border: 1px solid #edeff2;
  outline: none;
  box-shadow: none;
  padding: 11px 16px;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  color: #668091;
  width: 100%;
  background: #fff;
}
.wbk_coupon__feedback {
  margin-top: 4px;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
.wbk_coupon__feedback--success {
  background: #e6f7ec;
  color: #1a7f37;
  border: 1px solid #b7ebc6;
}
.wbk_coupon__feedback--failed {
  background: #fff1f0;
  color: #d93025;
  border: 1px solid #ffccc7;
}