@keyframes sidebarItemEnter {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.wbk_sidebar__wrapper {
  height: 100%;
  position: relative;
  transition: 0.3s all linear;
}
.wbk_sidebar__wrapper--open {
  width: 250px;
}
.wbk_sidebar__wrapper--hidden {
  width: 0;
}
@media screen and (max-width: 768px) {
  .wbk_sidebar__wrapper--open {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    z-index: 1;
  }
}
.wbk_sidebar__toggle-button {
  background-color: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(63, 63, 70, 0.1607843137);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: -12px;
  transition: 0.3s all linear;
  z-index: 1;
  transform: rotate(180deg);
}
.wbk_sidebar__toggle-button--hidden {
  transform: rotate(0deg);
}
.wbk_sidebar__inner-wrapper {
  padding: 24px;
  width: 250px;
  background-color: var(--wbk-secondary-500);
  border-left: 1px solid #edeff2;
  height: 100%;
  position: relative;
  transition: 0.3s all linear;
  display: flex;
  flex-flow: column;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.wbk_sidebar__inner-wrapper--open {
  width: 250px;
}
@media screen and (max-width: 768px) {
  .wbk_sidebar__inner-wrapper--open {
    width: 100%;
  }
}
.wbk_sidebar__inner-wrapper--hidden {
  width: 0;
  visibility: hidden;
  overflow: hidden;
  display: none;
}
.wbk_sidebar__mobile-close-button {
  width: 24px;
  height: 24px;
  cursor: pointer;
  position: absolute;
  right: 24px;
  top: 26px;
  display: none;
}
@media screen and (max-width: 768px) {
  .wbk_sidebar__mobile-close-button {
    display: block;
  }
}
.wbk_sidebar__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5%;
  vertical-align: middle;
  color: var(--wbk-secondary-text-600) !important;
  margin: 0;
  margin-bottom: 24px;
  text-align: left !important;
}
@media screen and (max-width: 768px) {
  .wbk_sidebar__title {
    padding-bottom: 24px;
    border-bottom: 1px solid #edeff2;
  }
}
.wbk_sidebar__items, .wbk_sidebar__items__inner {
  display: flex;
  flex-flow: column;
  margin-bottom: 24px;
}
.wbk_sidebar__items__inner {
  max-height: 360px;
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: #d1d5db transparent;
}
.wbk_sidebar__items__inner::-webkit-scrollbar {
  width: 6px;
  background: transparent;
}
.wbk_sidebar__items__inner::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 4px;
}
.wbk_sidebar__items__inner::-webkit-scrollbar-thumb:hover {
  background: #b0b8c1;
}
.wbk_sidebar__items__item {
  display: flex;
  gap: 16px;
  justify-content: flex-end;
  align-items: flex-start;
  width: 100%;
}
.wbk_sidebar__items__item:not(:last-child) {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #edeff2;
}
.wbk_sidebar__items__item--animated {
  animation: sidebarItemEnter 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.wbk_sidebar__items__item__info {
  margin-right: auto;
}
.wbk_sidebar__items__item__title, .wbk_sidebar__items__item__price {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  margin: 0;
  color: var(--wbk-secondary-text-600);
  margin-bottom: 4px !important;
  margin-right: auto;
  text-transform: none !important;
}
.wbk_sidebar__items__item__price {
  white-space: nowrap;
  margin-right: unset;
}
.wbk_sidebar__items__item__subline {
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1%;
  vertical-align: middle;
  color: var(--wbk-secondary-text-500);
  margin: 0;
  margin-bottom: 0 !important;
}
.wbk_sidebar__items__item__button-remove {
  cursor: pointer;
  display: inline-flex;
  margin-top: 6px;
}
.wbk_sidebar__items__item__button-remove img {
  width: 12px;
  height: 12px;
  max-width: 12px;
  max-height: 12px;
  min-width: 12px;
  min-height: 12px;
  object-fit: contain;
}
.wbk_sidebar__empty {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  margin: 0;
  color: var(--wbk-secondary-text-500);
  margin-bottom: 4px;
  text-align: left !important;
}
.wbk_sidebar__help {
  margin-top: auto;
}
.wbk_sidebar__help__title {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  color: var(--wbk-secondary-text-500);
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid var(--wbk-secondary-text-400);
  word-break: break-word;
}
.wbk_sidebar__help__text {
  display: flex;
  flex-flow: column;
  gap: 10px;
}
.wbk_sidebar__help__text a {
  text-decoration: none;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--wbk-secondary-text-500);
  word-break: break-word;
}
.wbk_sidebar__bottom-summary__wrapper {
  width: 100%;
  max-width: 750px;
  margin: 0 auto;
  z-index: 10;
  background: var(--wbk-secondary-500);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 24px 12px 24px;
  border-top: 1px solid #edeff2;
  border-radius: 0 0 8px 8px;
  gap: 16px;
  min-height: 56px;
}
@media screen and (max-width: 768px) {
  .wbk_sidebar__bottom-summary__wrapper {
    align-items: stretch;
    padding: 12px 8px;
    max-width: 100%;
    border-radius: 0 0 8px 8px;
  }
}
.wbk_sidebar__bottom-summary__total-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2px;
}
.wbk_sidebar__bottom-summary__label {
  font-size: 13px;
  color: var(--wbk-secondary-text-500);
  font-weight: 400;
  margin-bottom: 2px;
}
.wbk_sidebar__bottom-summary__total {
  font-size: 18px;
  color: var(--wbk-secondary-text-600);
  font-weight: 500;
  margin-bottom: 2px;
}
.wbk_sidebar__bottom-summary__subline {
  font-size: 12px;
  color: var(--wbk-secondary-text-800);
  font-weight: 300;
}
.wbk_sidebar__bottom-summary__show-summary-button {
  color: var(--wbk-secondary-text-500) !important;
}
.wbk_sidebar__bottom-summary__show-summary-button img {
  width: 12px;
  height: 12px;
  min-width: 12px;
  min-height: 12px;
  max-width: 12px;
  max-height: 12px;
}