.loadingContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  min-height: 750px;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.98);
  border-radius: 8px;
}

.spinner {
  border-radius: 50%;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #1f6763;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
.spinner.small {
  width: 20px;
  height: 20px;
  border-width: 2px;
}
.spinner.medium {
  width: 32px;
  height: 32px;
  border-width: 3px;
}
.spinner.large {
  width: 48px;
  height: 48px;
  border-width: 4px;
}

.message {
  margin: 0;
  font-size: 1rem;
  color: #666;
  text-align: center;
  font-weight: 500;
  margin-top: 0.5rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}