.webbaDataTable {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
  margin-top: 24px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.webbaDataTable tbody > tr {
  background-color: #fff;
  border: 1px solid #f3f4f6;
}
.webbaDataTable tbody > tr:last-child {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.webbaDataTable tbody > tr:last-child td:first-child {
  border-bottom-left-radius: 12px;
}
.webbaDataTable tbody > tr:last-child td:last-child {
  border-bottom-right-radius: 12px;
}

.tableHead {
  display: none;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.tableHeader {
  padding: 15px;
  cursor: pointer;
  text-align: left;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  font-style: normal;
  color: #4b5563;
}

.tableHeaderContent {
  display: flex;
  align-items: center;
  gap: 5px;
}

.tableHeader:first-child {
  border-radius: 10px 0 0 10px;
}

.tableHeader:last-child {
  border-radius: 0 10px 10px 0;
}

.tableCell {
  padding: 15px;
  text-align: left;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 300;
  font-style: normal;
}
.tableCell:first-child {
  max-width: 50px;
  width: 50px;
}
.tableCell:last-child {
  text-align: right;
}

.tableRowExpanded > td {
  padding: 0;
}

.menuBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: opacity 200ms, transform 500ms;
  padding: 0;
  min-width: unset;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menuBtn:active {
  transform: scale(0.9);
}
.menuBtn[data-title] {
  position: relative;
}
.menuBtn[data-title]::after {
  content: attr(data-title);
  display: flex;
  padding: 2px 5px;
  border-radius: 2px;
  background-color: rgba(17, 24, 39, 0.8666666667);
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(0) translate(-50%, -100%);
  white-space: nowrap;
}
.menuBtn[data-title]:hover::after {
  top: calc(-100% - 15px);
  right: 0;
  left: unset;
  transform: translateX(-25%) scale(1);
}

.menuBtn img {
  width: 20px;
  height: 20px;
  min-width: 20px;
  min-height: 20px;
  max-width: 20px;
  min-height: 20px;
}

.menuBtn:hover img {
  opacity: 0.7;
}

.menuBtn.hidden {
  visibility: hidden;
}

.menuBtn.open {
  transform: rotate(180deg);
}

.tableTitleContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .tableTitleContainer {
    flex-flow: column;
    align-items: flex-start;
  }
}

.addItemButton {
  border: none;
  background-color: green;
  color: white;
  padding: 10px;
  font-size: 18px;
  border-radius: 12px;
  cursor: pointer;
}

.tableTitle {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 40px;
  margin: 0;
}

.expandedData {
  padding: 15px;
}

.selectAllCheckboxMobile {
  margin-bottom: 15px;
}

.toolPanel {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 16px;
}
@media screen and (max-width: 768px) {
  .toolPanel {
    white-space: nowrap;
    flex-flow: column;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 10px;
    width: 100%;
    margin-top: 20px;
  }
  .toolPanel input[id=search] {
    width: 100%;
  }
}

.paginationContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
}

.itemCountWrapper {
  display: flex;
  align-items: center;
  gap: 8px;
}
.itemCountWrapper p {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0%;
  color: #4b5563;
}
@media screen and (max-width: 768px) {
  .itemCountWrapper p:nth-child(3) {
    display: none;
  }
}
.itemCountWrapper select {
  border-radius: 4px;
  border: 1px solid #e5e7eb;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  color: #1f2937;
}

.paginationTools {
  display: flex;
  align-items: center;
  gap: 8px;
}

.pagination {
  display: flex;
  align-items: stretch;
  justify-content: center;
  gap: 8px;
  flex-wrap: wrap;
}

.paginationTools button {
  border: 1px solid #e5e7eb;
  border-radius: 4px;
  padding: 8px 12px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  color: #1f2937;
  transition: 0.3s all linear;
  background-color: transparent;
}
.paginationTools button:hover, .paginationTools button.active {
  background-color: #1f6763;
  color: white;
}
.paginationTools button:not(:disabled) {
  cursor: pointer;
}
.paginationTools button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}
.paginationTools button img {
  display: none;
}
@media screen and (max-width: 768px) {
  .paginationTools button img {
    display: flex;
  }
  .paginationTools button span {
    display: none;
  }
}

.tableHead {
  display: table-header-group;
}

.tableContainer {
  box-shadow: 0 0 15px rgba(161, 164, 182, 0.5);
  border-radius: 20px;
  padding: 32px;
  margin: 20px;
  background: #f9fafb;
}
@media screen and (max-width: 768px) {
  .tableContainer {
    margin: 10px;
    padding: 25px;
  }
}

.selectAllCheckboxMobile input {
  display: none;
}

.menu {
  display: inline-flex;
  align-items: center;
  justify-content: flex-end;
  row-gap: 1px;
  column-gap: 12px;
  width: 170px;
  max-width: 170px;
}
@media screen and (max-width: 768px) {
  .menu .expandButton {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .menu {
    width: auto;
    max-width: unset;
  }
}

@media (max-width: 1400px) {
  .tableWrapper {
    overflow-x: auto;
    overflow-y: clip;
  }
}
.tableWrapper.horizontalOverflow {
  overflow-x: auto;
}

.rowItemTitle {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0%;
  color: #111827;
  margin: 0;
  word-wrap: break-word;
}

.rowItemContent {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0%;
  color: #6b7280;
  margin: 0;
  word-wrap: break-word;
}

.noWrapContainer * {
  white-space: nowrap;
}

.bulkActionsOuterWrapper {
  position: fixed;
  bottom: 0;
  right: 0;
  width: calc(100% - 160px);
  z-index: 999;
}
@media (max-width: 960px) {
  .bulkActionsOuterWrapper {
    width: calc(100% - 36px);
  }
}
@media (max-width: 782px) {
  .bulkActionsOuterWrapper {
    width: 100%;
  }
}

.bulkActionsWrapper {
  width: 100%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border: 1px solid #e5e7eb;
  border-bottom: none;
  padding: 17px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}
@media (max-width: 960px) {
  .bulkActionsWrapper {
    flex-flow: column;
    padding: 10px;
    gap: 10px;
  }
}
.bulkActionsWrapper .selectedItemsCount {
  display: flex;
  align-items: center;
  gap: 10px;
}
.bulkActionsWrapper .selectedItemsCount p {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0%;
  color: #4b5563;
  margin: 0;
}
.bulkActionsWrapper .selectedItemsCount span {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
  color: #6b7280;
  cursor: pointer;
}
.bulkActionsWrapper .selectedItemsCount span img {
  width: 12px;
  height: 12px;
  object-fit: contain;
}
.bulkActionsWrapper .bulkActionButtons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 12px;
}
.bulkActionsWrapper .bulkButtonWrapper {
  padding: 0;
}
.bulkActionsWrapper .bulkButton {
  background-color: #fff;
  color: #ef4444;
  border: 1px solid #ef4444;
  padding: 11px 17px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
  text-align: center;
}
.bulkActionsWrapper select {
  padding: 11px 17px;
  border-radius: 8px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0%;
}