.wbk_input__file__drop-area {
  border: 1.5px dashed #edeff2;
  border-radius: 8px;
  height: 120px;
  width: 100%;
  padding: 26px;
  display: flex;
  align-items: center;
  flex-flow: column;
  justify-content: center;
  gap: 4px;
  cursor: grab;
  margin-top: 16px;
}
.wbk_input__file__drop-area:active {
  cursor: grabbing;
}
.wbk_input__file__drop-area p,
.wbk_input__file__drop-area span {
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  text-align: center;
  vertical-align: middle;
  color: #22292f;
  margin: 0;
}
.wbk_input__file__drop-area span {
  color: #668091;
}
.wbk_input__file__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-top: 16px;
  display: flex;
  flex-flow: column;
  gap: 8px;
}
.wbk_input__file__list li {
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  color: #668091;
}
.wbk_input__file__list__item {
  display: flex;
  align-items: center;
  background: #f7f9fa;
  border: 1px solid #edeff2;
  border-radius: 6px;
  padding: 8px 12px;
  gap: 12px;
  transition: box-shadow 0.2s;
  position: relative;
  box-shadow: 0 1px 2px rgba(34, 41, 47, 0.03);
}
.wbk_input__file__list__item__name {
  flex: 1;
  font-weight: 400;
  color: #22292f;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.wbk_input__file__list__item__size {
  font-size: 13px;
  color: #668091;
  margin-left: 8px;
}
.wbk_input__file__list__item__remove-btn {
  background: none;
  border: none;
  color: #e74c3c;
  font-size: 18px;
  cursor: pointer;
  margin-left: 8px;
  padding: 2px 6px;
  border-radius: 50%;
  transition: background 0.15s;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wbk_input__file__list__item__remove-btn:hover {
  background: #fdecea;
  color: #c0392b;
}