.wrapper {
  padding: 32px;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
}
.wrapper h2 {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0%;
  text-align: center;
  color: #111827;
  margin: 0;
}
.wrapper p {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0%;
  text-align: center;
  color: #6b7280;
  margin: 0;
}

.heading {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.heading .iconWrapper {
  width: 64px;
  height: 64px;
  min-width: 64px;
  min-height: 64px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--wbk-primary-100);
}

.form {
  margin-top: 30px;
  display: flex;
  flex-flow: column;
  gap: 24px;
}
.form .fieldWrapper {
  display: flex;
  flex-flow: column;
  gap: 8px;
}
.form label {
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  letter-spacing: 0%;
  color: #374151;
}
.form [type=text],
.form [type=email],
.form [type=password] {
  background-color: #ffffff;
  border-radius: 10px;
  border: 1px solid #d1d5db;
  padding: 12px 16px;
  outline: none;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0%;
  vertical-align: middle;
  color: #374151;
  transform: 0.3s all ease;
}
.form [type=text]:active, .form [type=text]:focus,
.form [type=email]:active,
.form [type=email]:focus,
.form [type=password]:active,
.form [type=password]:focus {
  outline: none;
  background-color: #ffffff;
  border-color: var(--wbk-primary-500);
}
.form [type=text]::placeholder,
.form [type=email]::placeholder,
.form [type=password]::placeholder {
  color: #adaebc;
}

.optionsWrapper {
  margin-top: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.optionsWrapper .rememberLabel {
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0%;
  color: #4b5563;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.optionsWrapper .rememberLabel input {
  outline: none;
}
.optionsWrapper .rememberLabel input:active, .optionsWrapper .rememberLabel input:focus {
  outline: none;
}

.buttonWrapper {
  margin-top: 24px;
}
.buttonWrapper .button {
  width: 100%;
}

.error {
  text-align: center;
  margin-top: 15px;
  font-size: 16px;
  padding: 4px 8px;
  border-radius: 8px;
  background: #f8dcdc;
  color: #ff2a2a;
  border: 1px solid #ffadad;
  margin-top: 24px !important;
}