.wbk_progressbar__indicator {
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}
.wbk_progressbar__steps-list {
  display: flex;
  gap: 4px;
  align-items: center;
  justify-content: flex-end;
}
.wbk_progressbar__steps-list__step {
  transition: 0.3s all linear;
  width: 24px;
  height: 24px;
  min-width: 24px;
  min-height: 24px;
  background-color: #f9fafb;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 1%;
  text-align: center;
  color: #b0bfc9;
  border-radius: 50%;
  border: 1.5px solid #f9fafb;
}
.wbk_progressbar__steps-list__step--active {
  border-color: var(--wbk-primary-500);
  color: var(--wbk-primary-500);
  background-color: var(--wbk-primary-50);
}
.wbk_progressbar__steps-list__step--done {
  background-color: var(--wbk-primary-500);
  border-color: var(--wbk-primary-500);
  color: var(--wbk-primary-50);
}
.wbk_progressbar__progress-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: 100px;
  background-color: #edeff2;
  height: 4px;
  width: 100%;
}
.wbk_progressbar__progress-wrapper__progress {
  height: 100%;
  border-radius: 100px;
  background-color: var(--wbk-primary-500);
}
.wbk_progressbar__content h3 {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.5%;
  color: #22292f;
  margin: 0;
  margin-bottom: 4px;
  text-align: left !important;
}
.wbk_progressbar__content p {
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1%;
  color: #668091;
  margin: 0 !important;
}