.wbk_input__number__inner-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 8px;
}
.wbk_input__number__input-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.wbk_input__number__input {
  height: 40px;
  max-height: 40px;
  border: 1.5px solid #edeff2 !important;
  text-align: center;
  outline: none !important;
  box-shadow: none;
  border-radius: 0;
  max-width: 48px;
  background-color: #ffffff !important;
}
.wbk_input__number__quantity-button {
  width: 40px;
  max-width: 40px;
  min-width: 40px;
  height: 40px;
  max-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid #edeff2;
  cursor: pointer;
  background-color: #ffffff;
}
.wbk_input__number__quantity-button--increase {
  border-left: none;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.wbk_input__number__quantity-button--reduce {
  border-right: none;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
@media screen and (max-width: 768px) {
  .wbk_input__number__input {
    height: 30px;
    max-height: 30px;
    max-width: 28px;
  }
  .wbk_input__number__quantity-button {
    width: 30px;
    max-width: 30px;
    min-width: 30px;
    height: 30px;
    max-height: 30px;
  }
}