.wbk_button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  padding: 12px 16px;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5%;
  text-align: center;
  vertical-align: middle;
  transition: 0.3s all cubic-bezier(0.25, 0.8, 0.25, 1);
  text-decoration: none !important;
  position: relative;
}
.wbk_button:active, .wbk_button:focus {
  outline: none;
  box-shadow: none;
}
.wbk_button:active {
  transform: scale(0.95);
}
.wbk_button:hover:not(.disabled):not(:active) {
  transform: translateY(-4px) scale(1.03) perspective(400px) rotateX(4deg);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.18), 0 1.5px 4px rgba(0, 0, 0, 0.1);
}
.wbk_button--primary {
  background-color: var(--wbk-primary-500);
  color: #fff;
}
.wbk_button--primary:hover:not(.disabled) {
  background-color: var(--wbk-primary-600);
  color: #fff;
}
.wbk_button--secondary {
  background-color: #edeff2;
  color: #22292f;
}
.wbk_button--secondary:hover {
  background-color: var(--wbk-primary-500);
  color: var(--wbk-primary-50);
}
.wbk_button--generic {
  border: none;
  background-color: transparent;
  color: #668091;
  box-shadow: none;
  outline: none;
  position: static;
  transition: none;
}
.wbk_button--generic:hover:not(.disabled):not(:active) {
  transform: none;
  box-shadow: none;
  color: #668091;
  background-color: transparent !important;
}
.wbk_button--generic:active:not(.disabled) {
  transform: none;
  box-shadow: none;
  color: #668091;
  background-color: transparent !important;
}
.wbk_button--error {
  background-color: #dc2626;
  color: #fff;
}
.wbk_button--error:hover {
  background-color: #b91c1c;
  color: #fff;
}
.wbk_button--disabled {
  background-color: #edeff3;
  cursor: not-allowed;
}
.wbk_button:hover .wbk_button__tooltip {
  visibility: visible;
  z-index: 9999;
  opacity: 1;
}
.wbk_button__tooltip {
  background: #fff;
  color: #222;
  padding: 8px 14px;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  font-size: 14px;
  white-space: pre-line;
  pointer-events: none;
  transition: all 0.3s ease;
  text-align: center;
  min-width: 180px;
  max-width: 320px;
  position: absolute;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  bottom: calc(100% + 12px);
  right: 30%;
  margin-left: 12px;
}
.wbk_button__tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: calc(100% - 16px);
  transform: translateX(-50%);
  border-width: 6px;
  border-style: solid;
  border-color: #fff transparent transparent transparent;
}
.wbk_button__loader {
  width: 20px;
  height: 20px;
  border: 3px solid #fff;
  border-top: 3px solid #668091;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}