.wbk_categories__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.wbk_categories__items {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 4px;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.wbk_categories__items::-webkit-scrollbar {
  display: none;
}
.wbk_categories__items__item {
  padding: 4px 12px;
  border-radius: 100px;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  text-align: center;
  color: #22292f;
  background-color: #edeff2;
  cursor: pointer;
  border: 1.5px solid #edeff2;
  white-space: nowrap;
}
.wbk_categories__items__item--selected {
  border-color: var(--wbk-primary-500);
  background-color: var(--wbk-primary-50);
  color: var(--wbk-primary-500);
}
.wbk_categories__navigation-button {
  background-color: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(63, 63, 70, 0.1607843137);
  width: 24px;
  height: 24px;
  min-width: 24px;
  min-height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.wbk_categories__navigation-button--prev {
  left: -12px;
}
.wbk_categories__navigation-button--next {
  right: -12px;
}