.wbk_timeslots {
  gap: 8px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(99px, 1fr));
  padding: 16px;
}
.wbk_timeslots--noitems {
  padding: 0;
}
.wbk_timeslots__item {
  border-radius: 4px;
  border: 1px solid #edeff2;
  background-color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 1%;
  vertical-align: middle;
  color: #22292f;
  transition: 0.2s all linear;
  padding: 9px 18px;
  white-space: nowrap;
  width: 100%;
  min-width: 100px;
  cursor: pointer;
  text-align: center;
  flex: 1 0 120px;
  max-width: 180px;
  box-sizing: border-box;
}
.wbk_timeslots__item--selected {
  border-color: var(--wbk-primary-500);
  color: var(--wbk-primary-500);
  background-color: var(--wbk-primary-50);
}
.wbk_timeslots__item--max-reached, .wbk_timeslots__item--disabled {
  background-color: #f8f9fa;
  border-color: #e9ecef;
  color: #6c757d;
  cursor: not-allowed;
  opacity: 0.6 !important;
}
.wbk_timeslots__slot-appear {
  animation: slotSlideFadeIn 0.5s cubic-bezier(0.23, 1, 0.32, 1) both;
}
.wbk_timeslots__free-places {
  font-size: 12px;
  color: var(--wbk-primary-500);
}
.wbk_timeslots__skeleton-slot {
  height: 36px;
  flex: 0 0 120px;
  width: 100%;
  margin-bottom: 8px;
  border-radius: 6px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 400% 100%;
  background-position: 100% 50%;
  animation: skeleton-loading 1.2s infinite linear !important;
}
@media screen and (max-width: 768px) {
  .wbk_timeslots {
    grid-template-columns: repeat(3, minmax(50px, 1fr));
  }
}

@keyframes skeleton-loading {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
@keyframes slotSlideFadeIn {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}